.themeInputWrapper {
  position: relative;
}
.themeInputWrapper .themeSelect {
  position: absolute;
  top: 110%;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  padding: 1em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.7), 0px 0px 5px rgba(0, 0, 0, 0.4);
          box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.7), 0px 0px 5px rgba(0, 0, 0, 0.4);
  z-index: 2;
}
.themeInputWrapper .themeSelect p:last-of-type.addTheme {
  border: 1px solid red;
  color: red;
  border-radius: inherit;
}
.dummyInput {
  display: block;
  outline: none;
  border: none;
  width: 100%;
  -webkit-box-shadow: 0px 0px 1px 1px #4b66d7;
          box-shadow: 0px 0px 1px 1px #4b66d7;
  padding: .4em;
  border-radius: 4px;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 1.2em;
  height: 2em;
}



