.appHeader {
  width: 100%;
  height: 8vh;
  background-color: #444;
  position: fixed;
  color: #ddd;
  line-height: 8vh;
  text-align: center;
  font-size: 1.3em;
}
.appHeader > span:first-child {
  position: absolute;
  left: 5%;
  top: 0;
  bottom: 0;
  margin: auto;
  display: inline-block;
}
.appHeader > span:first-child::before {
  font-family: 'icomoon' !important;
  content: "\e900";
}



