.wrapper {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}
.wrapper .modal {
  position: absolute;
  width: 80%;
  max-height: 60%;
  overflow: scroll;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  background-color: #fff;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  max-width: 1080px;
  padding: 3em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.wrapper .modal .closeBtn {
  position: absolute;
  right: 2vh;
  top: 2vh;
  color: #000;
  font-size: 2em;
}
.wrapper .modal form label {
  display: block;
  margin: .5em 0;
}
.wrapper .modal form input {
  display: block;
  outline: none;
  border: none;
  width: 100%;
  -webkit-box-shadow: 0px 0px 1px 1px #4b66d7;
          box-shadow: 0px 0px 1px 1px #4b66d7;
  padding: .4em;
  border-radius: 4px;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 1.2em;
}
.wrapper .modal form input:focus,
.wrapper .modal form input:active {
  outline-offset: 0;
  outline: none;
  border: none;
  -webkit-box-shadow: 0px 0px 3px 1px #4b66d7;
          box-shadow: 0px 0px 3px 1px #4b66d7;
}
.wrapper .modal form button {
  padding: .5em;
  border-radius: 4px;
  background-color: #63c876;
  color: #fff;
  width: 15%;
}
.wrapper .photoContainer {
  margin-top: 1em;
}
.wrapper .photoContainer > div {
  display: inline-block;
  width: 10rem;
  height: 10rem;
  vertical-align: middle;
  margin: .5rem;
  position: relative;
}
.wrapper .photoContainer > div > img {
  max-width: 100%;
}
.wrapper .photoContainer > div > span {
  color: #fff;
  position: absolute;
  right: 0;
  top: 0;
  background-color: red;
  width: 1.5em;
  height: 1.5em;
  line-height: 1.5em;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
}
.wrapper .photoContainer > div > span:hover {
  background-color: #ff6666;
}
.wrapper .photoContainer > .addPhotoBtn {
  font-size: 8em;
  line-height: 8rem;
  text-align: center;
  color: #999;
  outline: 1px dashed #999;
}



